const mapStateToProps = (state: RootState) => ({
	partnerFormState: state.partnerReducer,
	formData: state.partnerReducer.formData,
	submitFormState: state.partnerReducer.sendFormData,
	stateList: state.partnerReducer.stateList,
	cityList: state.partnerReducer.cityList
});

const mapDispatchToProps = {
	clearState: ClearStateAction.action,
	updateForm: UpdateFormAction.action,
	changeStateOrCityAction: ChangeStateOrCityAction.action,
	sendFormData: SendFormDataAction.action
};

export default withStyles(Style, { name: 'DmartFreshSupplier' })(
	withRouter(
		connect(
			(state: RootState) => mapStateToProps,
			mapDispatchToProps
		)(withSnackbar(withGoogleReCaptcha(Supplier)))
	)
);
